import FooterComponentVue from "@/components/Footer/FooterComponent.vue";
import FechaComponent from "@/components/Fecha/FechaComponent.vue";
export default {
  data() {
    return {
      role: "",
      cuil: "",
      ancho: "",
      margin: "",
      posision: "",
      derecha: "",
      asd: this.$route.fullPath,
      notLogin: false,
    };
  },
  created() {
    // window.addEventListener("token-localstorage-changed", (event) => {
    //   this.role = event.detail.storage;
    // });
    this.role = localStorage.getItem("role") || null;

    // if (asd != "/login") {
    //   // this.ancho = 100;
    //   // this.margin = 0;
    //   this.ancho = 81.2;
    //   this.margin = 14;
    //   this.posision = "absolute";
    //   this.derecha = 0;
    // } else if (asd === "/login" || asd === "/") {
    //   this.ancho = 100;
    //   this.margin = 0;
    //   // this.ancho = 81.2;
    //   // this.margin = 14;
    //   this.posision = "relative";
    //   this.derecha = 0;
    // }

    // console.log(this.login);
  },
  watch: {
    $route() {
      this.role = localStorage.getItem("role");
    },
    // asd(newValue) {
    //   if (newValue != "/login") {
    //     this.ancho = 81.2;
    //     this.margin = 0;
    //     this.posision = "relative";
    //     this.derecha = 0;
    //   } else {
    //     this.ancho = 100;
    //     this.margin = 14;
    //     this.posision = "absolute";
    //     this.derecha = 0;
    //   }
    // },
  },
  computed: {
    setPermission() {
      if (this.$store.state.loggedIn === true) {
        return true;
      } else {
        return false;
      }
    },
    setClass() {
      // console.log(this.asd);
      if (this.asd === "/login" || this.asd !== "/") {
        return true;
      } else {
        return false;
      }
    },
  },
  components: {
    NavComponent,
    NavTopVue,
    // NavMunicipalesComponentVue,
    FooterComponentVue,
    FechaComponent,
  },
};
</script>

